.main-section-newpage {
  background-color: #636363;
  color: white;
  text-align: center;
  width: 100%;
  padding: 1%;
  align-items: center;
}

.cuestionario {
  padding-left: 30%;
  border-left: solid white;
}

.maps-section {
  display: flex;
  justify-content: center;
  gap: 40px;
  flex-wrap: wrap;
  margin-top: 30px;
  padding-inline: 10%;
  max-width: 100%;
  width: 100%;
}

.maps-section iframe {
  max-width: 100%;
  width: 100%;
  height: 400px;
  border-radius: 8px;
  pointer-events: auto;
}

.maps-section p {
  text-align: center;
  color: #ffffff;
}

h2 {
  margin-bottom: 20px;
  font-size: 2rem;
}

.tick {
  margin-right: 10px;
  color: #ffffff;
}

.fila-txt-form {
  align-items: center;
  padding-top: 2%;
  border-top: solid white;
  border-bottom: solid white;
  width: 100%;
  background-color: #000;
  border-width: 0.1rem;
}

.bloque-texto1 {
  text-align: center;
  color: white;
  width: 30%;
  padding-left: 15%;
  padding-right: 2%;
  align-items: stretch;
}

ul {
  list-style-type: disc;
  margin-left: 0;
  padding-left: 0;
  text-align: center;
}

ul li {
  font-size: 1rem;
  margin-bottom: 10px;
  display: inline-block;
}

button {
  display: block;
  margin: 20px auto;
}

.label-separado {
  margin-bottom: 1.5rem;
}

.bloque-3 {
  color: white;
  border-top: solid white;
  border-bottom: solid white;
  border-width: 1px;
  padding-top: 20px;
  padding-bottom: 10px;
  text-align: left;
  font-size: 16px;
  background-color: #000;
  width: 100%;
}

/* Responsividad para pantallas pequeñas */
@media (max-width: 768px) {
  .bloque-texto1 {
    width: 80%;
    padding-left: 10%;
    padding-right: 10%;
    text-align: center;
  }

  .cuestionario {
    padding-left: 10%;
    border-left: none;
  }

  .maps-section {
    padding-inline: 5%;
  }

  .fila-txt-form {
    font-size: 0.9rem;
    padding-top: 5%;
  }

  h2 {
    font-size: 1.5rem;
  }
}

@media (max-width: 480px) {
  .bloque-texto1 {
    padding-left: 5%;
    padding-right: 5%;
    width: 100%;
    text-align: center;
  }

  ul li {
    display: block;
    margin-bottom: 15px;
  }

  .maps-section iframe {
    height: 300px;
  }
}





